import { Token } from '@uniswap/sdk';
import { BigNumber, ethers } from 'ethers';
import { IAssetConfig } from '../../../models/AssetConfig';
import { CharacterTrait, INftDetails, loadNftDetailsFromUri } from '../../../models/NftDetails';
import { EthereumService } from '../../../services/EthereumService';
import { MessagingService } from '../../../services/MessagingService';
import { Utils } from '../../../services/Utils';
import { INonFungibleAssetService } from './Interfaces';

export class NonFungibleAssetService implements INonFungibleAssetService {
  walletAddress?: string;
  contract?: ethers.Contract;
  boostPaymentContract?: ethers.Contract;
  count: number = 0;
  gasPrice?: string | number;
  token: Token;
  tokenUri?: string;
  isEmployApproved: boolean = false;
  isBoostApproved: boolean = false;

  nfts: INftDetails[] = [];

  constructor(
    public config: IAssetConfig,
    private cropConfig: IAssetConfig,
    private paymentConfig: IAssetConfig,
    private messagingService: MessagingService,
    private ethereumService: EthereumService
  ) {
    this.walletAddress = this.ethereumService!.walletData.walletAddress;
    this.contract = new ethers.Contract(
      this.config.contractAddress,
      this.config.assetConstants!.contractAbi,
      ethereumService.signer
    );

    this.boostPaymentContract = new ethers.Contract(
      this.paymentConfig.contractAddress,
      this.paymentConfig.assetConstants!.contractAbi,
      this.ethereumService.signer!
    );

    this.token = new Token(
      ethereumService.networkManager!.network!.chainId,
      this.config.contractAddress,
      this.config.assetConstants!.decimals
    );

    this.setupEventHandlers();
  }

  update(): Promise<NonFungibleAssetService> {
    return this.refreshNfts().then(() => this);
  }

  async refreshNfts() {
    const tokenCount = await this.contract!.balanceOf(this.walletAddress);

    var nftPromises = [...Array(tokenCount.toNumber()).keys()].map(index => {
      return this.refreshNft(index);
    });

    this.nfts = await Promise.all(nftPromises);

    this.isEmployApproved = await this.contract!.isApprovedForAll(this.walletAddress, this.cropConfig.contractAddress);
    var boostAllowance = await this.boostPaymentContract!.allowance(
      this.walletAddress,
      this.config.contractAddress
    );
    this.isBoostApproved = boostAllowance !== BigNumber.from(0);
  }

  async refreshNft(index: number): Promise<INftDetails> {
    const tokenId = await this.contract!.tokenOfOwnerByIndex(this.walletAddress, BigNumber.from(index));
    const tokenUri = await this.contract!.tokenURI(tokenId.toNumber());
    const traits = await this.contract!.collectibleTraits(tokenId);
    return await loadNftDetailsFromUri(index, tokenId, tokenUri, traits);
  }

  mint(amount: number) {
    var fee = amount * this.ethereumService.networkManager!.network!.characterPrice;
    let overrides = {
      value: Utils.parseUnits(fee.toString())
    };

    return this.contract!.mintCollectible(BigNumber.from(amount), overrides).then(
      () =>
        this.messagingService.events.next({
          message: `Minting of ${this.config.assetConstants?.symbol} is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error minting ${this.config.assetConstants?.symbol} : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  approveActivate(nftConfig: IAssetConfig): Promise<any> {
    var nftContract = new ethers.Contract(
      nftConfig.contractAddress,
      nftConfig.assetConstants!.contractAbi,
      this.ethereumService.signer!
    );

    return nftContract.setApprovalForAll(this.cropConfig.contractAddress, 1).then(
      () =>
        this.messagingService.events.next({
          message: `Approval of ${this.config.assetConstants?.symbol} is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error approving ${this.config.assetConstants?.symbol} : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  async activate(nftTypeCode: number, nftDetails: INftDetails, cropConfig: IAssetConfig) {
    const tokenId = await this.contract!.tokenOfOwnerByIndex(this.walletAddress, BigNumber.from(nftDetails.index));

    var cropContract = new ethers.Contract(
      cropConfig.contractAddress,
      cropConfig.assetConstants!.contractAbi,
      this.ethereumService.signer!
    );

    return cropContract.equipCollectible(tokenId, nftTypeCode).then(
      () =>
        this.messagingService.events.next({
          message: `${this.config.assetConstants?.symbol} activation is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error activating ${this.config.assetConstants?.symbol} : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  approveBoost(nftConfig: IAssetConfig): Promise<any> {
    var amount = Utils.parseUnits('1000000000');

    return this.boostPaymentContract!.approve(nftConfig.contractAddress, amount).then(
      () =>
        this.messagingService.events.next({
          message: `Approval of ${this.config.assetConstants?.symbol} trait boost is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error approving ${this.config.assetConstants?.symbol} trait boost: (${Utils.getContractErrorMessage(
            error
          )})`,
          error: error
        })
    );
  }

  async boost(nftDetails: INftDetails, trait: CharacterTrait, boost: number) {
    const tokenId = await this.contract!.tokenOfOwnerByIndex(this.walletAddress, BigNumber.from(nftDetails.index));

    return this.contract!.boostTrait(tokenId, BigNumber.from(trait), BigNumber.from(boost)).then(
      () =>
        this.messagingService.events.next({
          message: `Boosting of ${this.config.assetConstants?.symbol} is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error boosting ${this.config.assetConstants?.symbol} : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  removeEventHandlers() {
    if (this.contract) {
      this.contract!.removeAllListeners();
    }
  }

  setupEventHandlers() {
    this.removeEventHandlers();

    var update = (message: string) =>
      this.update().then(() => {
        this.messagingService.events.next({ message: message });
      });

    if (this.boostPaymentContract) {
      this.boostPaymentContract!!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('Approval(address,address,uint256)')]
        },
        (owner: any, spender: any, amount: any) => {
          if (owner === this.ethereumService.walletData.walletAddress) {
            update(`${this.config.assetConstants?.symbol} trait boost`);
          }
        }
      );
    }

    if (this.contract) {
      this.contract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('ApprovalForAll(address,address,bool)')]
        },
        (owner, operator, approved) => {
          if (owner === this.ethereumService.walletData.walletAddress) {
            if (approved) {
              update(`${this.config.assetConstants?.symbol}s have been approved`);
            } else {
              this.messagingService.errors.next({
                message: `${this.config.assetConstants?.symbol}s were not approved`,
                error: `${this.config.assetConstants?.symbol}s were not approved`
              });
            }
          }
        }
      );

      this.contract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('Transfer(address,address,uint256)')]
        },
        (from, to, tokenId) => {
          if (to === this.ethereumService.walletData.walletAddress) {
            update(`${this.config.assetConstants?.symbol} available`);
          }
        }
      );
    }
  }
}
