import React, { Component } from 'react';
import { INftDetails } from '../../../models/NftDetails';

export interface ICharacterAction {
  name: string;
  isApproved: boolean;
  onClick: any;
}
export interface ICharacterProps {
  nft?: INftDetails;
  actions: ICharacterAction[];
  onCardClick?: any;
  isSample: boolean;
}

export class Character extends Component<ICharacterProps> {
  constructor(props: any) {
    super(props);
    this.click = this.click.bind(this);
  }

  click() {
    this.props.onCardClick(this.props.nft);
  }

  render() {
    return (
      <div className="stage mr-3 mb-5 is-flex is-flex-direction-column is-align-items-center noselect">
        <div
          onClick={this.click}
          className={`card p-3 ${this.rarityClass()}`}
          style={{
            backgroundImage: `linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 1)),url(${this.props.nft!.image})`
          }}>
          <div className="card-image">
            <figure className="image is-3by3">
              <img className="nft" src={this.props.nft!.image} alt="Farmland Collectible" />
              {this.props.isSample && <h3 className="nft-sample">Sample</h3>}
            </figure>
          </div>
          <div className="card-content p-0 py-2">
            <div className="content">
              <p className="character-title is-4 mb-0 mt-2">{this.props.nft!.name}</p>
              <p className="is-size-6 mb-0">{this.description()}</p>
              <div className="attributes is-flex is-flex-direction-row is-align-items-self-end is-flex-wrap-wrap">
                {this.renderAttributes()}
              </div>
              <div className="is-flex is-flex-direction-column is-align-items-end has-text-weight-bold mt-2">
                {this.props.nft!.stats.map(s => {
                  return (
                    <div key={s.name} className="is-flex is-justify-content-space-between">
                      <p className="is-size-7 mb-0">{s.name}</p>
                      <p className={`${this.statClass(s.value)} is-size-7 mb-0`}>{s.value}</p>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </div>
        <div className="stage-action">
          <div className="is-flex is-flex-direction-row">
            {this.props.actions.map((a: ICharacterAction) => {
              return (
                <button
                  key={a.name}
                  className="button is-transparent-action activate-button"
                  onClick={() => a.onClick(this.props.nft)}
                  disabled={this.props.isSample}>
                  {!a.isApproved && <span>Approve {a.name}</span>}
                  {! !a.isApproved && <span>{a.name}</span>}
                </button>
              );
            })}
          </div>
        </div>
      </div>
    );
  }

  description(): string {
    var specialAttributes = this.props.nft!.attributes.filter(
      a => ['Special', 'Image', 'Stat'].indexOf(a.trait_type) >= 0
    ).length;
    if (specialAttributes > 3) return 'A legendary Farmland character';
    if (specialAttributes > 2) return 'An epic Farmland character';
    if (specialAttributes > 1) return 'A rare Farmland character';
    if (specialAttributes > 0) return 'An uncommon Farmland character';
    return 'A common Farmland character';
  }

  rarityClass(): string {
    var specialAttributes = this.props.nft!.attributes.filter(
      a => ['Special', 'Image', 'Stat'].indexOf(a.trait_type) >= 0
    ).length;
    if (specialAttributes > 3) return 'rarity legendary';
    if (specialAttributes > 2) return 'rarity epic';
    if (specialAttributes > 1) return 'rarity rare';
    if (specialAttributes > 0) return 'rarity uncommon';
    return '';
  }

  statClass(value: number): string {
    if (value > 94) return 'super-stat';
    if (value > 79) return 'good-stat';
    return '';
  }

  renderAttributes() {
    return (
      <React.Fragment>
        {this.props
          .nft!.attributes.filter(a => ['Special', 'Image', 'Stat'].indexOf(a.trait_type) >= 0)
          .map(a => {
            return (
              <span key={a.id} className={`tag is-primary ${a.trait_type}`}>
                {a.value}
              </span>
            );
          })}
      </React.Fragment>
    );
  }
}
