import React, { Component } from 'react';
import { CharacterTrait, INftDetails } from '../../../models/NftDetails';
import { NonFungibleAssetService } from '../Services/NonFungibleAssetService';

export interface ICharacterBoostProps {
  nftService: NonFungibleAssetService;
  nft: INftDetails;
  close: any;
}

export interface ICharacterBoostState {
  staminaBoostAmount: number;
  strengthBoostAmount: number;
  speedBoostAmount: number;
  courageBoostAmount: number;
  intelligenceBoostAmount: number;
  total: number;
  boosting: CharacterTrait;
}

export class CharacterBoost extends Component<ICharacterBoostProps, ICharacterBoostState> {
  constructor(props: any) {
    super(props);

    this.state = {
      staminaBoostAmount: 0,
      strengthBoostAmount: 0,
      speedBoostAmount: 0,
      courageBoostAmount: 0,
      intelligenceBoostAmount: 0,
      total: 0,
      boosting: CharacterTrait.Stamina
    };

    this.handleAmountChange = this.handleAmountChange.bind(this);
    this.handleBoostingChange = this.handleBoostingChange.bind(this);
    this.boost = this.boost.bind(this);
    this.close = this.close.bind(this);
  }

  handleAmountChange(trait: CharacterTrait, amount: number) {
    switch (trait) {
      case CharacterTrait.Stamina:
        this.setState({ staminaBoostAmount: this.state.staminaBoostAmount + amount, total: this.state.total + amount });
        break;
      case CharacterTrait.Strength:
        this.setState({
          strengthBoostAmount: this.state.strengthBoostAmount + amount,
          total: this.state.total + amount
        });
        break;
      case CharacterTrait.Speed:
        this.setState({ speedBoostAmount: this.state.speedBoostAmount + amount, total: this.state.total + amount });
        break;
      case CharacterTrait.Courage:
        this.setState({ courageBoostAmount: this.state.courageBoostAmount + amount, total: this.state.total + amount });
        break;
      case CharacterTrait.Intelligence:
        this.setState({
          intelligenceBoostAmount: this.state.intelligenceBoostAmount + amount,
          total: this.state.total + amount
        });
        break;
    }
  }

  handleBoostingChange(trait: CharacterTrait) {
    this.setState({
      boosting: trait,
      staminaBoostAmount: 0,
      strengthBoostAmount: 0,
      speedBoostAmount: 0,
      courageBoostAmount: 0,
      intelligenceBoostAmount: 0,
      total: 0
    });
  }

  boost(nft: INftDetails): void {
    if (this.state.boosting && this.state.total) {
      this.props.nftService.boost(this.props.nft, this.state.boosting!, this.state.total);
    }
  }

  close() {
    this.props.close();
  }

  render() {
    return (
      <div className="is-active modal">
        <div className="modal-background" onClick={this.close}></div>
        <div className="modal-content character-boost">
          <div className="card">
            <div className="is-flex is-justify-content-center character-boost-title mb-5">{this.props.nft.name}</div>
            <CharacterBoostField
              name="Stamina"
              current={this.props.nft.stats.find(s => s.name === 'Stamina')!.value}
              amount={this.state.staminaBoostAmount}
              total={this.state.total}
              trait={CharacterTrait.Stamina}
              isBoosting={this.state.boosting === CharacterTrait.Stamina}
              handleIsBoosting={this.handleBoostingChange}
              handleAmountChange={this.handleAmountChange}></CharacterBoostField>
            <CharacterBoostField
              name="Strength"
              current={this.props.nft.stats.find(s => s.name === 'Strength')!.value}
              amount={this.state.strengthBoostAmount}
              total={this.state.total}
              trait={CharacterTrait.Strength}
              isBoosting={this.state.boosting === CharacterTrait.Strength}
              handleIsBoosting={this.handleBoostingChange}
              handleAmountChange={this.handleAmountChange}></CharacterBoostField>
            <CharacterBoostField
              name="Speed"
              current={this.props.nft.stats.find(s => s.name === 'Speed')!.value}
              amount={this.state.speedBoostAmount}
              total={this.state.total}
              trait={CharacterTrait.Speed}
              isBoosting={this.state.boosting === CharacterTrait.Speed}
              handleIsBoosting={this.handleBoostingChange}
              handleAmountChange={this.handleAmountChange}></CharacterBoostField>
            <CharacterBoostField
              name="Courage"
              current={this.props.nft.stats.find(s => s.name === 'Courage')!.value}
              amount={this.state.courageBoostAmount}
              total={this.state.total}
              trait={CharacterTrait.Courage}
              isBoosting={this.state.boosting === CharacterTrait.Courage}
              handleIsBoosting={this.handleBoostingChange}
              handleAmountChange={this.handleAmountChange}></CharacterBoostField>
            <CharacterBoostField
              name="Intelligence"
              current={this.props.nft.stats.find(s => s.name === 'Intelligence')!.value}
              amount={this.state.intelligenceBoostAmount}
              total={this.state.total}
              trait={CharacterTrait.Intelligence}
              isBoosting={this.state.boosting === CharacterTrait.Intelligence}
              handleIsBoosting={this.handleBoostingChange}
              handleAmountChange={this.handleAmountChange}></CharacterBoostField>
            <div className="is-flex is-flex-direction-row is-justify-content-center mt-3">
              <button className="button mt-4 is-action" onClick={() => this.boost(this.props.nft)}>
                Boost
              </button>
            </div>
          </div>
        </div>
        <button className="modal-close is-large" aria-label="close" onClick={this.close}></button>
      </div>
    );
  }
}

export interface ICharacterBoostFieldProps {
  name: string;
  trait: CharacterTrait;
  current: number;
  amount: number;
  total: number;
  isBoosting: boolean;
  handleIsBoosting: any;
  handleAmountChange: any;
}

class CharacterBoostField extends Component<ICharacterBoostFieldProps> {
  render() {
    return (
      <div
        className={
          'is-flex is-flex-direction-row is-align-items-center is-justify-content-space-between mt-1 character-boost-field ' +
          (this.props.isBoosting ? 'is-active' : 'is-disabled')
        }>
        <label className="label mr-5 mb-0">
          <button className="button is-ghost-action" onClick={() => this.props.handleIsBoosting(this.props.trait)}>
            Boost {this.props.name}
            <span className={'ml-2 ' + (this.props.amount > 0 ? 'boosted' : '')}>
              ({this.props.current + this.props.amount})
            </span>
          </button>
        </label>
        <div className="field has-addons has-addons-centered mr-3">
          <p className="control">
            <button
              className="button is-small is-action"
              disabled={this.props.total <= 0 || this.props.amount <= 0 || !this.props.isBoosting}
              onClick={() => this.props.handleAmountChange(this.props.trait, -1)}>
              <span className="icon is-small">
                <i className="fas fa-minus"></i>
              </span>
            </button>
          </p>
          <p className="control mx-4 mt-1 has-action-text" style={{ minWidth: '20px', textAlign: 'center' }}>
            {' '}
            {this.props.amount}
          </p>
          <p className="control">
            <button
              className="button is-small is-action"
              disabled={
                this.props.total >= 10 ||
                this.props.amount >= 10 ||
                !this.props.isBoosting ||
                this.props.current + this.props.amount >= 99
              }
              onClick={() => this.props.handleAmountChange(this.props.trait, +1)}>
              <span className="icon is-small">
                <i className="fas fa-plus"></i>
              </span>
            </button>
          </p>
        </div>
      </div>
    );
  }
}
