import { Component } from 'react';
import './Message.scss';

interface IMessageProps {
  error: any;
  message: any;
  pending: any;
  hide: any;
}

class Message extends Component<IMessageProps> {
  timer: any[];

  constructor(props: IMessageProps) {
    super(props);

    this.timer = [];
  }

  componentWillReceiveProps(props: IMessageProps) {
    this.setState({ ...this.state, props });

    if (props.error) {
      console.log(props.error);
    }

    if (props.message) {
      if (this.timer[props.message]) {
        clearTimeout(this.timer[props.message]);
        this.timer[props.message] = null;
      }

      this.timer[props.message] = setTimeout(() => {
        this.props.hide();
      }, 5000);
    }
  }

  render() {
    return (
      <div>
        {this.props.message && (
          <div className="notification">
            <button className="delete" onClick={() => this.props.hide}></button>
            <span
              className={
                'icon mr-3 ' +
                (this.props.error ? 'has-text-danger' : this.props.pending ? 'has-text-warning' : 'has-text-success')
              }>
              <i
                className={
                  'fas ' +
                  (this.props.error
                    ? 'fa-radiation-alt'
                    : this.props.pending
                    ? 'fa-exclamation-circle'
                    : 'fa-info-circle')
                }></i>
            </span>
            <span
              className={
                this.props.error ? 'has-text-danger' : this.props.pending ? 'has-text-warning' : 'has-text-success'
              }>
              {this.props.message}
            </span>
          </div>
        )}
      </div>
    );
  }
}

export default Message;
